<template>
  <div class="data-panel def-shadow">

    <div class="row ">
      <div class="col-sm-12">
        <div class="table-header h4 d-inline float-left text-accent">全部</div>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-12">

        <table class="table data-table br-t-none">
          <thead class="br-t-none">
            <tr class="br-t-none">
              <th scope="col" class="text-center">发票编号</th>
              <th scope="col">客户名称</th>
              <th scope="col">菜品标签</th>
              <th scope="col" class="text-center">报价总金额</th>
              <th scope="col" class="text-center">报价日期</th>
              <th scope="col" class="text-center">状态</th>
              <th scope="col" class="text-center">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="f in data" :key="f.id">
              <th scope="row" class="text-center">{{ f.num }}</th>
              <td>{{ f.named }}</td>
              <td>
                <span v-for="t in f.tag" :key="t.named" class="tag tag-table" :style="{ 'background': t.color }">{{ t.named }} </span>
              </td>
              <td class="text-center">
                $ {{ f.result_price }}
              </td>
              <td class="text-center">
                {{ f.listing_time }}
              </td>
              <td class="text-center">
                {{ f.listing_status }}
              </td>
              <td class="text-center">
                
                
              </td>
            </tr>
          </tbody>
        </table>

      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'ListingViewDataTable',
  data() {
    return {
      data: [
        {
          id: 1, num: '00AAC', named: '潮乡', tag: [ { id: 1, named: '酒店', color: '#F07505 ' } ], result_price: '32.10', listing_time: '2020/12/8', add_time: '2020-12-8 17:00', listing_status: '草稿'
        },
        {
          id: 2, num: '00AAB', named: '潮乡 2', tag: [ { id: 2, named: '餐馆', color: '#AA06F2' } ], result_price: '29.10', listing_time: '2020/12/8', add_time: '2020-12-8 17:00', listing_status: '未结款'
        },
        {
          id: 3, num: '00AAA', named: '潮乡 3', tag: [ { id: 2, named: '餐馆', color: '#AA06F2' } ], result_price: '129.01', listing_time: '2020/12/10', add_time: '2020-12-8 17:00', listing_status: '已结款'
        },
      ]
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
</style>


